import { USERSERVICE_API } from "@/config/env";
import {
  UserDTO,
  LoginDTO,
  FriendReqDTO,
  FriendsResDTO,
} from "@/types/userService/user";

// Í≥µÌÜµ ÏùëÎãµ ÌÉÄÏûÖ
interface ApiResponse {
  success: boolean;
  message?: string;
}

// Ï§ëÎ≥µ Ï≤¥ÌÅ¨ ÏùëÎãµ ÌÉÄÏûÖ (Î∞±ÏóîÎìúÏóêÏÑú "exists" ÎòêÎäî "not exists" Í∞ôÏùÄ Î¨∏ÏûêÏó¥ Î∞òÌôò ÏòàÏÉÅ)
interface DuplicateCheckResponse {
  available: boolean;   // ÌîÑÎ°†Ìä∏ÏóêÏÑú Ïì∞Í∏∞ Ï¢ãÍ≤å ÌÜµÏùº
}

export interface UserProfile {
  userSignId: string;
  userName: string;
  nickName: string;
  email: string;
  profileImg: string | null;
}

/**
 * ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ Ï°∞Ìöå
 */
export async function getUserProfile(userSignId: string): Promise<UserProfile | null> {
  try {
    const response = await fetch(`${USERSERVICE_API}/profile/${userSignId}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });

    if (!response.ok) {
      console.error(`ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï°∞Ìöå Ïã§Ìå®: ${response.status}`);
      return null;
    }

    const data = await response.json();
    return data;
  } catch (error) {
    console.error('ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï°∞Ìöå Ï§ë Ïò§Î•ò:', error);
    return null;
  }
}

/**
 * Ïó¨Îü¨ ÏÇ¨Ïö©ÏûêÏùò ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ ÏùºÍ¥Ñ Ï°∞Ìöå
 */
export async function getUserProfiles(userSignIds: string[]): Promise<Map<string, UserProfile>> {
  const profileMap = new Map<string, UserProfile>();

  // Ï§ëÎ≥µ Ï†úÍ±∞
  const uniqueIds = [...new Set(userSignIds)];

  // Î≥ëÎ†¨Î°ú Ï°∞Ìöå
  const results = await Promise.allSettled(
      uniqueIds.map(id => getUserProfile(id))
  );

  results.forEach((result, index) => {
    if (result.status === 'fulfilled' && result.value) {
      profileMap.set(uniqueIds[index], result.value);
    }
  });

  return profileMap;
}

// 1. ÌöåÏõêÍ∞ÄÏûÖ
export async function signup(formData: FormData): Promise<ApiResponse> {
  const response = await fetch(`${USERSERVICE_API}/signup`, {
    method: "POST",
    // FormData ÏÇ¨Ïö© Ïãú Content-Type Ìó§ÎçîÎ•º ÏÑ§Ï†ïÌïòÏßÄ ÏïäÏùå (Î∏åÎùºÏö∞Ï†ÄÍ∞Ä ÏûêÎèôÏúºÎ°ú ÏÑ§Ï†ï)
    body: formData,
  });

  const text = await response.text();

  if (response.status === 201) {
    return { success: true, message: text || "ÌöåÏõêÍ∞ÄÏûÖ ÏÑ±Í≥µ" };
  }

  return { success: false, message: text || "ÌöåÏõêÍ∞ÄÏûÖ Ïã§Ìå®" };
}
// 2. Î°úÍ∑∏Ïù∏
export async function login(loginDto: LoginDTO): Promise<{
  userSignId: string;
  role: string;
  refreshToken: string;
  accessToken: string;
  profileImg?: string
}> {
  const response = await fetch(`${USERSERVICE_API}/login`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(loginDto),
  });

  if (!response.ok) {
    throw new Error(`Î°úÍ∑∏Ïù∏ Ïã§Ìå®: ${response.status}`);
  }

  const authHeader = response.headers.get("Authorization");
  const accessToken = authHeader?.startsWith("Bearer ") ? authHeader.substring(7) : "";
  const body = await response.json();

  return {
    userSignId: body.userSignId,
    role: body.role,
    refreshToken: body.refreshToken,
    accessToken,
    profileImg: body.profileImg,
  };
}

// 3. ÏïÑÏù¥Îîî Ï§ëÎ≥µ Ï≤¥ÌÅ¨ ‚Üí GET /existId?userId=xxx
export async function checkUserSignId(userSignId: string): Promise<DuplicateCheckResponse> {
  const response = await fetch(
      `${USERSERVICE_API}/existId?userId=${encodeURIComponent(userSignId)}`
  );

  const text = await response.text();
  const available = text.trim() === "not exists";
  return { available };
}

