// src/components/userService/RegisterUser.tsx
import React, { useState, useEffect } from 'react';
import styles from '@/styles/userService/Signup.module.css';
import { useForm } from "react-hook-form";
import { UserDTO } from "@/types/userService/user";
import { signup, checkUserSignId, checkEmail, checkNickName } from "@/api/userService/user";
import { useRouter } from "next/router";

type FormData = UserDTO & { passwordConfirm: string };

export default function RegisterUser() {
  const { register, handleSubmit, watch, formState: { errors, isValid }, setError, clearErrors, reset } = useForm<FormData>({ mode: "onChange" });
  const router = useRouter();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [serverError, setServerError] = useState("");

  // ÌååÏùº Í¥ÄÎ†® state Ï∂îÍ∞Ä
  const [profileImage, setProfileImage] = useState<File | null>(null);
  const [imagePreview, setImagePreview] = useState<string>("");

  const passwordValue = watch("password");
  const userSignIdValue = watch("userSignId");
  const emailValue = watch("email");
  const nickNameValue = watch("nickName");

  const [idStatus, setIdStatus] = useState<"idle" | "checking" | "available" | "taken">("idle");
  const [emailStatus, setEmailStatus] = useState<"idle" | "checking" | "available" | "taken">("idle");
  const [nickStatus, setNickStatus] = useState<"idle" | "checking" | "available" | "taken">("idle");

  // ÏïÑÏù¥Îîî Ï§ëÎ≥µ Ï≤¥ÌÅ¨
  useEffect(() => {
    if (!userSignIdValue || userSignIdValue.length < 4) { setIdStatus("idle"); return; }
    const t = setTimeout(async () => {
      setIdStatus("checking");
      try {
        const res = await checkUserSignId(userSignIdValue);
        setIdStatus(res.available ? "available" : "taken");
        res.available ? clearErrors("userSignId") : setError("userSignId", { type: "manual", message: "Ïù¥ÎØ∏ ÏÇ¨Ïö© Ï§ëÏù∏ ÏïÑÏù¥ÎîîÏòàÏöî" });
      } catch { setIdStatus("taken"); }
    }, 500);
    return () => clearTimeout(t);
  }, [userSignIdValue]);

  // Ïù¥Î©îÏùº Ï§ëÎ≥µ Ï≤¥ÌÅ¨
  useEffect(() => {
    if (!emailValue || !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(emailValue)) {
      setEmailStatus("idle");
      return;
    }
    const t = setTimeout(async () => {
      setEmailStatus("checking");
      try {
        const res = await checkEmail(emailValue);
        setEmailStatus(res.available ? "available" : "taken");
        res.available ? clearErrors("email") : setError("email", { type: "manual", message: "Ïù¥ÎØ∏ Í∞ÄÏûÖÎêú Ïù¥Î©îÏùºÏù¥ÏóêÏöî" });
      } catch {
        setEmailStatus("taken");
      }
    }, 500);
    return () => clearTimeout(t);
  }, [emailValue, clearErrors, setError]);

  // ÎãâÎÑ§ÏûÑ Ï§ëÎ≥µ Ï≤¥ÌÅ¨
  useEffect(() => {
    if (!nickNameValue || nickNameValue.length < 2) {
      setNickStatus("idle");
      return;
    }
    const t = setTimeout(async () => {
      setNickStatus("checking");
      try {
        const res = await checkNickName(nickNameValue);
        setNickStatus(res.available ? "available" : "taken");
        res.available ? clearErrors("nickName") : setError("nickName", { type: "manual", message: "Ïù¥ÎØ∏ ÏÇ¨Ïö© Ï§ëÏù∏ ÎãâÎÑ§ÏûÑÏù¥ÏóêÏöî" });
      } catch {
        setNickStatus("taken");
      }
    }, 500);
    return () => clearTimeout(t);
  }, [nickNameValue, clearErrors, setError]);

  // Ïù¥ÎØ∏ÏßÄ ÌååÏùº ÏÑ†ÌÉù Ìï∏Îì§Îü¨
  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    // ÌååÏùº ÌÉÄÏûÖ Í≤ÄÏ¶ù
    if (!file.type.startsWith('image/')) {
      alert('Ïù¥ÎØ∏ÏßÄ ÌååÏùºÎßå ÏóÖÎ°úÎìú Í∞ÄÎä•Ìï¥Ïöî');
      return;
    }

    // ÌååÏùº ÌÅ¨Í∏∞ Í≤ÄÏ¶ù (5MB)
    if (file.size > 20 * 1024 * 1024) {
      alert('ÌååÏùº ÌÅ¨Í∏∞Îäî 20MB Ïù¥ÌïòÎßå Í∞ÄÎä•Ìï¥Ïöî');
      return;
    }

    setProfileImage(file);

    // ÎØ∏Î¶¨Î≥¥Í∏∞ ÏÉùÏÑ±
    const reader = new FileReader();
    reader.onloadend = () => {
      setImagePreview(reader.result as string);
    };
    reader.readAsDataURL(file);
  };

  // Ïù¥ÎØ∏ÏßÄ Ï†úÍ±∞ Ìï∏Îì§Îü¨
  const handleRemoveImage = () => {
    setProfileImage(null);
    setImagePreview("");
  };

  const allValid = isValid && idStatus === "available" && emailStatus === "available" && nickStatus === "available";

  const onSubmit = async (data: FormData) => {
    if (!allValid) return;
    setIsSubmitting(true);
    setServerError("");

    try {
      const { passwordConfirm, profile_img, ...userData } = data;

      // FormData ÏÉùÏÑ±
      const formData = new FormData();

      // ÌÖçÏä§Ìä∏ ÌïÑÎìú Ï∂îÍ∞Ä
      Object.entries(userData).forEach(([key, value]) => {
        formData.append(key, value as string);
      });

      // ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ Ï∂îÍ∞Ä (ÏûàÎäî Í≤ΩÏö∞)
      if (profileImage) {
        formData.append('profileImage', profileImage);
      }

      const res = await signup(formData);

      if (res.success) {
        alert("ÌöåÏõêÍ∞ÄÏûÖ ÏôÑÎ£å! Momentory„Å∏ Ïò§Ïã† Í±∏ ÌôòÏòÅÌï©ÎãàÎã§");
        reset();
        handleRemoveImage();
        router.push("/");
      } else {
        setServerError(res.message || "ÌöåÏõêÍ∞ÄÏûÖÏóê Ïã§Ìå®ÌñàÏñ¥Ïöî");
      }
    } catch (error) {
      console.error('ÌöåÏõêÍ∞ÄÏûÖ ÏóêÎü¨:', error);
      setServerError("ÏÑúÎ≤ÑÏóê Î¨∏Ï†úÍ∞Ä ÏûàÏñ¥Ïöî. Ïû†Ïãú ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî");
    } finally {
      setIsSubmitting(false);
    }
  };

  const getStatusIcon = (status: typeof idStatus) => {
    if (status === "checking") return "Í≤ÄÏÇ¨ Ï§ë";
    if (status === "available") return "ÏÇ¨Ïö© Í∞ÄÎä•";
    if (status === "taken") return "ÏÇ¨Ïö© Î∂àÍ∞Ä";
    return null;
  };

  return (
      <div className={styles.container}>
        <div className={styles.card}>
          <h1 className={styles.title}>Momentory</h1>
          <p className={styles.subtitle}>Ïò§ÎäòÏùò ÏàúÍ∞Ñ, ÏòÅÏõêÌûà Í∞ÑÏßÅÌïòÎã§</p>

          <form onSubmit={handleSubmit(onSubmit)} className={styles.form} noValidate>

            {/* ÏïÑÏù¥Îîî */}
            <div className={styles.field}>
              <label className={styles.fieldLabel}>ÏïÑÏù¥Îîî</label>
              <div className={styles.inputWithStatus}>
                <input
                    {...register("userSignId", {
                      required: "ÌïÑÏàò ÏûÖÎ†•Ïù¥ÏóêÏöî",
                      minLength: { value: 4, message: "4Ïûê Ïù¥ÏÉÅ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî" }
                    })}
                    placeholder="4~20Ïûê ÏòÅÎ¨∏+Ïà´Ïûê"
                    className={`${styles.input} ${errors.userSignId ? styles.errorInput : ''}`}
                />
                {idStatus !== "idle" && (
                    <span className={`${styles.status} ${styles[idStatus]}`}>
                  {getStatusIcon(idStatus)}
                </span>
                )}
              </div>
              {errors.userSignId && <p className={styles.errorText}>{errors.userSignId.message}</p>}
            </div>

            {/* Ïù¥Î©îÏùº */}
            <div className={styles.field}>
              <label className={styles.fieldLabel}>Ïù¥Î©îÏùº</label>
              <div className={styles.inputWithStatus}>
                <input
                    {...register("email", {
                      required: "ÌïÑÏàò ÏûÖÎ†•Ïù¥ÏóêÏöî",
                      pattern: { value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i, message: "Ïù¥Î©îÏùº ÌòïÏãùÏù¥ ÏïÑÎãàÏóêÏöî" }
                    })}
                    type="email"
                    placeholder="example@naver.com"
                    className={`${styles.input} ${errors.email ? styles.errorInput : ''}`}
                />
                {emailStatus !== "idle" && (
                    <span className={`${styles.status} ${styles[emailStatus]}`}>
                  {getStatusIcon(emailStatus)}
                </span>
                )}
              </div>
              {errors.email && <p className={styles.errorText}>{errors.email.message}</p>}
            </div>

            <div className={styles.row}>
              {/* Ïù¥Î¶Ñ */}
              <div className={styles.field}>
                <label className={styles.fieldLabel}>Ïù¥Î¶Ñ</label>
                <input
                    {...register("userName", {
                      required: "Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî",
                      pattern: { value: /^[Í∞Ä-Ìû£a-zA-Z\s]+$/, message: "Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî" }
                    })}
                    placeholder="ÌôçÍ∏∏Îèô"
                    className={`${styles.input} ${errors.userName ? styles.errorInput : ''}`}
                />
                {errors.userName && <p className={styles.errorText}>{errors.userName.message}</p>}
              </div>

              {/* ÎãâÎÑ§ÏûÑ */}
              <div className={styles.field}>
                <label className={styles.fieldLabel}>ÎãâÎÑ§ÏûÑ</label>
                <div className={styles.inputWithStatus}>
                  <input
                      {...register("nickName", {
                        required: "ÎãâÎÑ§ÏûÑÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî",
                        minLength: { value: 2, message: "2Ïûê Ïù¥ÏÉÅ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî" }
                      })}
                      placeholder="Î™®Î©òÌÜ†Î¶¨Ïïà"
                      className={`${styles.input} ${errors.nickName ? styles.errorInput : ''}`}
                  />
                  {nickStatus !== "idle" && (
                      <span className={`${styles.status} ${styles[nickStatus]}`}>
                    {getStatusIcon(nickStatus)}
                  </span>
                  )}
                </div>
                {errors.nickName && <p className={styles.errorText}>{errors.nickName.message}</p>}
              </div>
            </div>

            <div className={styles.row}>
              {/* ÎπÑÎ∞ÄÎ≤àÌò∏ */}
              <div className={styles.field}>
                <label className={styles.fieldLabel}>ÎπÑÎ∞ÄÎ≤àÌò∏</label>
                <input
                    {...register("password", {
                      required: "ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî",
                      minLength: { value: 8, message: "8Ïûê Ïù¥ÏÉÅ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî" }
                    })}
                    type="password"
                    placeholder="ÌäπÏàòÎ¨∏ÏûêÎ•º Ìè¨Ìï®Ìïú 8Ïûê Ïù¥ÏÉÅ"
                    className={`${styles.input} ${errors.password ? styles.errorInput : ''}`}
                />
                {errors.password && <p className={styles.errorText}>{errors.password.message}</p>}
              </div>

              {/* ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ */}
              <div className={styles.field}>
                <label className={styles.fieldLabel}>ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏</label>
                <input
                    {...register("passwordConfirm", {
                      required: "ÌôïÏù∏Ìï¥ Ï£ºÏÑ∏Ïöî",
                      validate: v => v === passwordValue || "ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Îã¨ÎùºÏöî"
                    })}
                    type="password"
                    placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÌïúÎ≤àÎçî ÌôïÏù∏Ìï¥Ïöî"
                    className={`${styles.input} ${errors.passwordConfirm ? styles.errorInput : ''}`}
                />
                {errors.passwordConfirm && <p className={styles.errorText}>{errors.passwordConfirm.message}</p>}
              </div>
            </div>

            {/* ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú */}
            <div className={styles.field}>
              <label className={styles.fieldLabel}>
                ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ <span className={styles.optional}>(ÏÑ†ÌÉù)</span>
              </label>

              {imagePreview ? (
                  <div className={styles.imagePreviewContainer}>
                    <img src={imagePreview} alt="ÌîÑÎ°úÌïÑ ÎØ∏Î¶¨Î≥¥Í∏∞" className={styles.imagePreview} />
                    <button
                        type="button"
                        onClick={handleRemoveImage}
                        className={styles.removeImageBtn}
                    >
                      ÏÇ≠Ï†ú
                    </button>
                  </div>
              ) : (
                  <label className={styles.fileInputLabel}>
                    <input
                        type="file"
                        accept="image/*"
                        onChange={handleImageChange}
                        className={styles.fileInput}
                    />
                    <span className={styles.fileInputText}>
                    üì∑ Ïù¥ÎØ∏ÏßÄ ÏÑ†ÌÉù (jpg, png, gif - ÏµúÎåÄ 20MB)
                  </span>
                  </label>
              )}
            </div>

            {serverError && <div className={styles.serverError}>{serverError}</div>}

            <button
                type="submit"
                disabled={isSubmitting || !allValid}
                className={styles.submitBtn}
            >
              {isSubmitting ? "Í∞ÄÏûÖ Ï§ë..." : "ÏßÄÍ∏à ÏãúÏûëÌïòÍ∏∞"}
            </button>
          </form>
        </div>
      </div>
  );
}