// src/api/userService/friends.ts
import { FRIENDSSERVICE_API } from "@/config/env";
import type { FriendsResDTO, FriendReqDTO } from "@/types/userService/friends";

// 1. Ïù∏Ï¶ù ÌÜ†ÌÅ∞ÏùÑ Í∞ÄÏ†∏Ïò§Îäî Ìó¨Ìçº Ìï®Ïàò
const getAuthToken = (): string | null => {
  // TODO: Ïã§Ï†ú ÌîÑÎ°úÏ†ùÌä∏Ïùò Î°úÏßÅÏóê ÎßûÍ≤å Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄ, Ïø†ÌÇ§ Îì±ÏóêÏÑú JWT ÌÜ†ÌÅ∞ÏùÑ Í∞ÄÏ†∏Ïò§ÎèÑÎ°ù Íµ¨ÌòÑÌïòÏÑ∏Ïöî.
  const token = localStorage.getItem('accessToken');
  if (!token) {
    console.error("Authentication token not found.");
    return null;
  }
  return token;
};

// 2. Í≥µÌÜµ ÏöîÏ≤≠ Ìó§Îçî ÏÉùÏÑ± Ìï®Ïàò (Authorization Ìó§Îçî Ìè¨Ìï®)
const getAuthHeaders = () => {
  const token = getAuthToken();
  if (!token) {
    throw new Error("Ïù∏Ï¶ù ÌÜ†ÌÅ∞Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§. Î°úÍ∑∏Ïù∏ ÏÉÅÌÉúÎ•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.");
  }
  return {
    'Authorization': `Bearer ${token}`,
  };
};


// Í≥µÌÜµ ÏùëÎãµ ÌÉÄÏûÖ
interface ApiResponse {
  success: boolean;
  message?: string;
}

// 1. ÏπúÍµ¨ Í¥ÄÍ≥Ñ ÏÉÅÌÉú Ï°∞Ìöå
export async function getFriendshipStatus(
    currentUserSignId: string,
    targetUserSignId: string
): Promise<FriendsResDTO | null> {
  const headers = getAuthHeaders();

  const response = await fetch(
      `${FRIENDSSERVICE_API}/status?targetUserId=${encodeURIComponent(targetUserSignId)}`,
      {
        method: "GET",
        headers: headers,
      }
  );

  if (response.status === 204) {
    return null;
  }
  if (response.ok) {
    return await response.json();
  }
  const msg = await response.text();
  console.error(`Status Check Failed: HTTP ${response.status} - ${msg}`);
  throw new Error(msg || "ÏÉÅÌÉú Ï°∞Ìöå Ïã§Ìå®");
}

// 2. ÏπúÍµ¨ ÏöîÏ≤≠ Î≥¥ÎÇ¥Í∏∞
export async function sendFriendRequest(
    currentUserSignId: string,
    receiverSignId: string
): Promise<FriendsResDTO> {
  const headers = getAuthHeaders();

  const response = await fetch(`${FRIENDSSERVICE_API}/request`, {
    method: "POST",
    headers: {
      ...headers,
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ receiverSignId }),
  });

  if (response.ok) {
    return await response.json();
  }
  const msg = await response.text();
  console.error(`Send Request Failed: HTTP ${response.status} - ${msg}`);
  throw new Error(msg || "ÏπúÍµ¨ ÏöîÏ≤≠ Ïã§Ìå®");
}

// 3. Î∞õÏùÄ ÏπúÍµ¨ ÏöîÏ≤≠ Î™©Î°ù Ï°∞Ìöå (ÏàòÏ†ï: 204 Ï≤òÎ¶¨ Î∞è Î°úÍπÖ Ï∂îÍ∞Ä)
export async function getReceivedFriendRequests(): Promise<FriendsResDTO[]> {
  const headers = getAuthHeaders();

  const response = await fetch(`${FRIENDSSERVICE_API}/requests/received`, {
    method: "GET",
    headers: headers,
  });

  // ‚ú® 204 No Content Ï≤òÎ¶¨
  if (response.status === 204) {
    return [];
  }

  if (response.ok) {
    return await response.json();
  }

  // ‚ú® ÏóêÎü¨ Î°úÍπÖ Ï∂îÍ∞Ä
  const msg = await response.text();
  console.error(`Received Requests Failed: HTTP ${response.status} - ${msg}`);
  throw new Error(msg || `ÏπúÍµ¨ ÏöîÏ≤≠ Î™©Î°ù Ï°∞Ìöå Ïã§Ìå® (Status: ${response.status})`);
}

// 4. ÏπúÍµ¨ Î™©Î°ù Ï°∞Ìöå (ÏàòÏ†ï: 204 Ï≤òÎ¶¨ Î∞è Î°úÍπÖ Ï∂îÍ∞Ä)
